<template>
  <div class="hello">
    <img src="../assets/logo.png">
    <h1>{{ msg }}</h1>
    <ul v-for='item in routelist'>
      <li><router-link :to='item.to'>{{ item.label }}</router-link></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      msg: 'My Vue-Router Demo',
      routelist: [
        { to: '/demo1', label: '' },
        { to: '/demo2', label: '' },
        { to: '/demo3', label: '' },
        { to: '/demo4', label: '' },
        { to: '/demo5', label: '' },
        { to: '/demo6', label: '' },
        { to: '/demo7', label: '' },
        { to: '/demo8', label: 'props' },
        { to: '/demo9', label: '' },
        { to: '/demo10', label: '' },
        { to: '/demo11', label: '' },
        { to: '/demo12', label: '' },
        { to: '/demo12_1', label: '(2)' },
        { to: '/demo13', label: '' },
        { to: '/demo14', label: '' },
      ]
    }
  }
}
</script>

<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

a {
  color: #42b983;
}
</style>




